<template>
  <admin-template
    :pageName="pageName"
    :alias="alias"
    :breadcrumbs="breadcrumb"
    :cssClass="cssClass"
  >
    <router-view></router-view>
  </admin-template>
</template>

<script>
import AdminTemplate from '@/components/admin/Template'
import BarberMixin from '@/mixins/BarberMixin'
import ScheduleMixin from '@/mixins/ScheduleMixin'
export default {
  name: 'Admin',
  components: { AdminTemplate },
  mixins: [ BarberMixin, ScheduleMixin ],
  data: () => ({
    alias: null,
    pageName: null,
    breadcrumb: null,
    cssClass: null,
    qtdSchedulesPendingInterval: null
  }),
  watch: {
    barberState () {
      this.getQtdSchedulesPending()
    }
  },
  mounted () {
    this.configTemplate()
    this.getQtdSchedulesPending()
    const timeToSync = 1 * 60 * 1000 // 3 minutos
    this.qtdSchedulesPendingInterval = setTimeout(() => {
      this.getQtdSchedulesPending()
    }, timeToSync)
  },
  updated () {
    this.configTemplate()
  },
  methods: {
    configTemplate () {
      this.alias = this.$route.meta.alias || ''
      this.pageName = this.$route.meta.pageName
      this.breadcrumb = this.$route.meta.breadcrumb || null
      this.cssClass = this.$route.meta.css || ''
    },
    getQtdSchedulesPending () {
      if (this.barberState) {
        this.$axios.get(`/schedule/barbershop/${this.barberState.barbershop_id}/pending/total`)
        .then(response => {
          if (response.status === 200 && response.data.codigo === 200) {
            this.setQtdRequestsPendingState(response.data.corpo)
          }
        })
      }
    }
  }
}
</script>