<template>
  <v-app id="admin-template">
    <navigation-drawer
      ref="navigationDrawer"
      :alias="alias"
      :itemsMenu="itemsMenu"
    />
    <topo
      :avatar="avatar"
      :items="topMenuItems"
      :name="barberName"
      :notificationItems="notificationItems"
      :title="pageName"
      :subtitle="barbershopName"
      @clicked="onClickedTopMenu"
      @toggleMenu="toggleMenu"
    />
    <v-content
      v-if="isPermissionAdmin"
      class="background main"
    >
      <v-container
        class="container"
        :class="cssClass"
        fluid
      >
        <div class="conteudo">
          <breadcrumb
            v-if="breadcrumbs"
            :items="breadcrumbs"
            class="mb-4 mt-0 py-0 d-none d-md-block" 
          />
          <v-row
            v-if="btnRegister"
            class="admin-register-content d-flex-center"
          >
            <v-spacer></v-spacer>
            <v-col cols="auto" class="align-self-center">
              <btn-secondary
                text="cadastrar barbearia"
                icon="add"
                class="btn-register"
                @clicked="registerBarbershop"
              />
            </v-col>
            <v-spacer></v-spacer>
          </v-row>
          <slot v-if="content"></slot>
        </div>
      </v-container>
    </v-content>
    <register-barbershop-dialog ref="registerBarbershopDialog" />
    <app-footer
      v-show="alias"
      class="app-footer"
      :items="footerItems" 
      @clicked="changePage"
      :bottomNav="alias"
    />
  </v-app>
</template>

<script>
import Axios from "@/plugins/axios"
import AppFooter from '@/components/admin/Footer'
import Breadcrumb from '@/components/globais/breadcrumb/Breadcrumb'
import BtnSecondary from '@/components/globais/buttons/secondary/BtnSecondary'
import NavigationDrawer from '@/components/admin/NavigationDrawer'
import RegisterBarbershopDialog from '@/components/admin/barbershop/RegisterBarbershopDialog'
import Topo from '@/components/admin/topo/Topo'
import BarberMixin from '@/mixins/BarberMixin'
import BarbershopMixin from '@/mixins/BarbershopMixin'
import NavigationMixin from '@/mixins/NavigationMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'

export default {
  name: 'Template',
  mixins: [ BarberMixin, BarbershopMixin, NavigationMixin, SnackbarMixin ],
  components: { AppFooter, Breadcrumb, BtnSecondary, NavigationDrawer, RegisterBarbershopDialog, Topo },
  props: {
    alias: { default: '' },
    breadcrumbs: { default: Array },
    btnRegister: { default: false },
    cssClass: { default: 'px-4 px-sm-8' },
    pageName: { default: 'Meu Barbeiro' },
    content: { default: true }
  },
  data: () => ({
    footerItems: [
      { title: 'Agendamentos', icon: 'schedule', value: 'schedules', route: 'admin-dashboard' },
      { title: 'Solicitaes', icon: 'event_note', value: 'scheduling_requests', route: 'admin-scheduling-requests' },
      { title: 'Faturamento', icon: 'paid', value: 'revenues', route: 'admin-revenues' }
    ],
    requiresAdmin: false,
    topMenuItems: [
      { title: 'Meus Dados', alias: 'admin-profile' },
      { title: 'Sair', alias: 'logout' }
    ]
  }),
  computed: {
    avatar () {
      return this.barber.image_url
    },
    barberName () {
      return this.getBarber().name
    },
    barbershopName () {
      return (this.barbershop) ? this.barbershop.name : null
    },
    isAdmin () {
      if (this.barber && this.barbershop) {
        if (this.barber.id === this.barbershop.admin_id) return true
      }
      return false
    },
    isPermissionAdmin () {
      return !this.requiresAdmin || this.isAdmin
    },
    itemsMenu () {
      let items = [{ icon: 'dashboard', text: 'Dashboard', routeName: 'admin-dashboard', alias: 'dashboard' }]
        
      if (this.isAdmin) {
        items.push({ icon: 'people_alt', text: 'Barbeiros', routeName: 'admin-barber', alias: 'barber' })
        items.push({ icon: 'miscellaneous_services', text: 'Config', routeName: 'admin-barbershop', alias: 'barbershop' })
        items.push({ icon: 'build', text: 'Preos e Servios', routeName: 'admin-service', alias: 'service' })
      }

      items.push({ icon: 'help', text: 'Ajuda', routeName: 'admin-help', alias: 'help' })
      return items
    },
    menuOpen () {
      return this.getDrawer()
    },
    notificationItems () {
      return this.$store.getters['NotificationStore/getNotification']
    }
  },
  created () {
    this.$store.dispatch('DashboardStore/init')
    this.$store.dispatch('AdminSchedulesStore/init')
    this.requiresAdmin = this.$route.meta.requiresAdmin
    this.barbershop = this.getBarbershop()
    this.getSchedules()
    setTimeout(() => {
      this.getSchedules()
    }, 60000)
  },
  updated () {
    this.requiresAdmin = this.$route.meta.requiresAdmin
  },
  mounted () {
    let barber = this.getBarber()
    if (!this.isAutenticated()) {
      this.showSnackbarErro('Usurio no autenticado!')
      this.logout()
    }
    if (barber.barbershop_id) this.getBarberShopService()
  },
  methods: {
    getBarberShopService () {
      Axios.get('/barbershop/' + this.barber.barbershop_id)
      .then(response => {
        if (response.data.codigo === 200) {
          let barbershop = response.data.corpo
          this.storeBarbershop(barbershop)
        } else this.setError(response.data.corpo)
      })
      .catch ((err) => {
        window.console.log(err)
        this.setError('No foi possvel recuperar os dados da barbearia!')
      })
    },
    getSchedules () {
      Axios.get(`/schedule/barbershop/${this.barber.barbershop_id}/pending`)
      .then(response => {
        if (response.data.codigo === 200) {
          let items = response.data.corpo.length
          this.$store.dispatch('NotificationStore/setNotification', items)
        } else window.console.log(response.data.corpo)
      })
      .catch ((err) => { window.console.log(err) })
    },
    onClickedTopMenu (item) {
      if (item.alias === 'logout') this.logout()
      else this.goTo(item.alias)
    },
    toggleMenu () {
      this.$refs.navigationDrawer.toggleNavigationDrawer()
    },
    changePage (item) {
      this.goTo(item.route)
    },
    registerBarbershop () {
      this.$refs.registerBarbershopDialog.showDialog()
    },
    setError (text) {
      this.showSnackbarErro(text)
    }
  }
}
</script>

<style lang="scss" scoped>
@import 'src/scss/theme.scss';

.v-list { background: transparent !important; }
.admin-register-content { height: calc(100vh - 150px);
  .btn-register { width: auto; }
}
@media all and (min-width: 960px) {
  #menu-lateral { width: 90px !important; }
  nav#menu-lateral + header + .main,
  nav#menu-lateral ~ .app-footer {
    padding-left: 90px !important;
    @include transition-mixin (all, .5s);
  }
  nav#menu-lateral + header + .main { padding-top: 64px !important; }
}

@media all and (max-width: 959px) {
  main { padding-top: 40px !important; padding-bottom: 56px !important; }
}
</style>